    <div class="main">
        <div class="elements">
            <div class="tableview tablepane">
                <form id="translations-table" method="post" accept-charset="UTF-8" saveshortcut data-confirm-unload>
                    <table class="data fullwidth">
                        <thead>
                            <tr>
                                <th v-for="column in columns" v-html="column.title" @click="orderBy(column.handle)" :class="{ 'ordered': column.handle == order.column, 'asc': column.handle == order.column && order.direction == 'asc', 'desc': column.handle == order.column && order.direction == 'desc' }"></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="sourceMessage in sourceMessages">
                                <td v-for="column in columns">

                                    <template v-if="sourceMessage[column.handle]">
                                        {{ sourceMessage[column.handle] }}
                                    </template>
                                    
                                </td>
                                <!-- <td v-html="sourceMessage.category"></td>
                                <td v-html="sourceMessage.message"></td>
                                <td v-for="locale in locales">
                                    <input class="text fullwidth" type="text" v-if="sourceMessage.messages.filter(message => message.language == locale).length" v-model="sourceMessage.messages.filter(message => message.language == locale)[0].translation">
                                </td>
                                <td v-html="sourceMessage.dateCreated"></td>
                                <td v-html="sourceMessage.lastUpdated"></td> -->
                            </tr>
                            <!-- <tr v-for="sourceMessage in sourceMessages">
                                <td v-html="sourceMessage.category"></td>
                                <td v-html="sourceMessage.message"></td>
                                <td v-for="locale in locales">
                                    <input class="text fullwidth" type="text" v-if="sourceMessage.messages.filter(message => message.language == locale).length" v-model="sourceMessage.messages.filter(message => message.language == locale)[0].translation">
                                </td>
                                <td v-html="sourceMessage.dateCreated"></td>
                                <td v-html="sourceMessage.lastUpdated"></td>
                            </tr> -->
                        </tbody>
                    </table>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

// Our component exports
export default {
    components: {
    },
    props: {
    },
    methods: {
        orderBy(column) {
            console.log(column)
        },
        getColumns() {
            let firstColumns = [
                {
                    title: 'Category',
                    handle: 'category'
                },
                {
                    title: 'Message',
                    handle: 'message'
                }
            ];
            let lastColumns = [
                {
                    title: 'Created',
                    handle: 'dateCreated'
                },
                {
                    title: 'Updated',
                    handle: 'dateUpdated'
                }
            ];

            let columns = [];

            firstColumns.forEach((column) => {
                columns.push(column)
            })

            this.locales.forEach((column) => {
                columns.push({
                    title: column,
                    handle: 'language_' + column
                })
            })

            lastColumns.forEach((column) => {
                columns.push(column)
            })

            this.columns = columns;
            console.log(this.columns)
        },
        getSourceMessages() {
            let self = this;
            axios.get('database-translations/api/index')
            .then(function (response) {
                self.sourceMessages = response.data.sourceMessages;
                self.locales = response.data.locales;
                self.categories = response.data.categories;

                console.log(self.sourceMessages)

                self.getColumns();
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })
            .then(function () {
                // always executed
            });
        },
        updateData() {
            // database-translations/translations/update
        }
    },
    created: function() {
        this.getSourceMessages();
    },
    data: function() {
        return {
            locales: [],
            columns: [],
            sourceMessages: [],
            messages: [],
            order: {
                column: 'category',
                direction: 'asc'
            }
        }
    },
}
</script>
